const pluginsRouter = [{
    path: '/datepicker',
    component: resolve => require(['@/components/datepicker/datepicker'], resolve),
    meta: {
      title: '时间选择器',
      requireAuth: true
    }
  },
  {
    path: '/RecyclerviewNew',
    component: resolve => require(['@/components/recyclerviewNew/RecyclerviewNew'], resolve),
    meta: {
      title: 'RecyclerviewNew',
      requireAuth: true
    }
  },
  {
    path: '/eleDatepicker',
    component: resolve => require(['@/components/ele/eleDatepicker'], resolve),
    meta: {
      requireAuth: true,
      title: 'element时间选择器'
    }
  },
  {
    path: '/upload',
    component: resolve => require(['@/components/ele/upload'], resolve),
    meta: {
      requireAuth: true,
      title: '文件上传'
    }
  }
]

export default pluginsRouter



