import Vue from 'vue'
import Router from 'vue-router'

import chartRouter from '@/router/charts' //chart部分路由
import pluginsRouter from '@/router/plugins'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '*',
      redirect: '/index'
    },
    {
      path: '/',
      redirect: '/login',
      component: resolve => require(['@/pages/index'], resolve),
      meta: {
        title: '登录',
        requireAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['@/pages/login'], resolve),
      meta: {
        title: '登录'
      }
    },
    {
      path: '/index',
      name: 'index',
      component: resolve => require(['@/pages/index'], resolve),
      meta: {
        title: '首页',
        requireAuth: true
      },
      children: [
        ...chartRouter,
        ...pluginsRouter
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title;
    next();
    if (to.meta.requireAuth) {
      if (sessionStorage.getItem('username')) {
        next();
      } else {
        next({
          path: '/login'
        });
      }
    } else {
      next();
    }
  } else {
    next();
  }
})

export default router



